.page-header {
  position: fixed;
  right: 0;
  left: 0;
  z-index: 30;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 auto;
  max-width: 1222px;
  padding: 28px 63px 35px 0;

  @media (max-width: $vp-1024) {
    padding: 20px 26px;
    justify-content: space-between;
    display: block;
  }

  @media (max-width: $vp-770) {
    // display: block;
  }

  @media (max-width: $vp-320) {
    padding: 15px 0 0 10px;
  }
}

.page-header__nav {
  display: flex;

  @media (max-width: $vp-1024) {
    padding-left: 25px;
    padding-bottom: 5px;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  @media (max-width: $vp-320) {
    padding-left: 16px;
    padding-bottom: 20px;
  }
}

.page-header__logo img {
  @media (max-width: 767px) {
    width: 38px;
    height: 38px;
  }
}

.page-header__container-open-modal {
  background: transparent;
  padding: 10px 15px;
  color: $light-color;
  text-transform: uppercase;
  font-family: $font--roboto-bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  border: 1px solid $light-color;
  border-radius: 3px;
  cursor: pointer;
  min-height: 40px;
  margin-top: 5px;

  @media (max-width: $vp-1024) {
    display: none;
  }
}
