.site-sections__link {
  font: 400 16px/135% "PTMono", "Courier", monospace;
  color: rgba(255, 255, 255, 0.7);
  outline: none; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .site-sections__link:hover, .site-sections__link:focus {
      color: #ffffff;
      text-decoration: underline; } }

.communications {
  display: flex;
  flex-direction: column;
  align-items: flex-start; }

.communications__title {
  margin: 2px 0 5px;
  padding: 0 40px;
  font: 400 15px/16px "RobotoCondensed", "Arial", sans-serif;
  color: #ffffff;
  letter-spacing: 0.05em;
  text-transform: uppercase; }
  @media (max-width: 769px) {
    .communications__title {
      margin-bottom: 11px;
      padding: 0; } }

.communications__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  list-style: none; }
  @media (max-width: 769px) {
    .communications__list {
      display: none;
      padding-left: 0;
      margin: 0; } }

.communications__address {
  margin-bottom: 5px;
  color: rgba(255, 255, 255, 0.7);
  font-style: normal; }
  @media (max-width: 769px) {
    .communications__address {
      font: 400 14px/135% "PTMono", "Courier", monospace;
      margin-bottom: 22px; } }

.communications__tel {
  margin-bottom: 10px;
  color: rgba(255, 255, 255, 0.7);
  font-style: normal;
  outline: none; }
  @media (max-width: 769px) {
    .communications__tel {
      font: 400 14px/135% "PTMono", "Courier", monospace;
      margin-bottom: 12px; } }
  @media (hover: hover), screen and (min-width: 0\0) {
    .communications__tel:hover, .communications__tel:focus {
      color: #ffffff;
      text-decoration: underline; } }

